/* src/components/Projects.css */
.projects-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  text-align: center;
}

.projects-container h1 {
  font-size: 2.5rem;
  margin-bottom: 2rem;
  color: #333;
}

.projects-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  justify-content: center;
}

.project-card {
  background-color: #f9f9f9;
  padding: 1.5rem;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  width: calc(33.333% - 2rem);
}

.project-card h2 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  color: #333;
}

.project-card p {
  font-size: 1rem;
  line-height: 1.5;
  margin-bottom: 1rem;
  color: #666;
}

.project-card a {
  color: #61dafb;
  text-decoration: none;
}

.project-card a:hover {
  text-decoration: underline;
}

