/* src/components/Footer.css */
.footer {
  background-color: #282c34;
  color: white;
  padding: 1rem;
  text-align: center;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
}

.footer a {
  color: #61dafb;
  text-decoration: none;
  margin: 0 0.5rem;
}

.footer a:hover {
  text-decoration: underline;
}

.footer-content p {
  margin: 0.5rem 0;
}

.footer-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.footer-links {
  margin-top: 0.5rem;
}

.footer-links a {
  color: #61dafb;
  margin: 0 0.5rem;
  text-decoration: none;
}

.footer-links a:hover {
  text-decoration: underline;
}

.content {
  padding-bottom: 3rem; /* Make space for the fixed footer */
}

